import React, { useState } from 'react';
import { DEVuser } from '../../DevUtility/user';

const EditProfile = ({
  nick = DEVuser.nick,
  firstName = DEVuser.firstName,
  lastName = DEVuser.lastName,
  email = DEVuser.email,
  phone = DEVuser.phone,
  opinion = undefined,
}) => {
  const [user, setUser] = useState({
    nick,
    firstName,
    lastName,
    email,
    phone,
    opinion,
  });

  const onChangeUser = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };
  return (
    <div className="contaniner profileContainer editProfileContainer">
      <div className="imapgeProfile">
        <img
          className={'image-user'}
          src="https://randomuser.me/api/portraits/women/68.jpg"
          alt="uytkownik"
        />
        <div className="profileButtons">
          <button className="btn btn-green btn-capitalize">Edytuj</button>
        </div>
      </div>
      <form className="userProfile editUserProfile">
        <div>
          <p>nick:</p>
          <input
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="wprowadz nick ..."
            type="text"
            name="nick"
            value={user.nick}
            key={nick}
          />
        </div>
        <div>
          <p>imi:</p>
          <input
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="wprowadz imi ..."
            type="text"
            name="firstName"
            value={user.firstName}
            key={firstName}
          />
        </div>
        <div>
          <p>nazwisko:</p>
          <input
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="wprowadz nazwisko ..."
            type="text"
            name="lastName"
            value={user.lastName}
            key={lastName}
          />
        </div>
        <div>
          <p>email:</p>
          <input
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="wprowadz email ..."
            type="text"
            name="email"
            value={user.email}
            key={email}
          />
        </div>
        <div>
          <p>tel:</p>
          <input
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="wprowadz tel ..."
            type="text"
            name="phone"
            value={user.phone}
            key={phone}
          />
        </div>
        <div>
          <p>opinia:</p>
          <textarea
            className="editUserOpinionTextArea"
            onChange={(e) => {
              onChangeUser(e);
            }}
            placeholder="Tu moesz wystawi opini o stadninie. Wystawiajc opini WYRASZ ZGOD na upoblicznienie swojego wizerunku. Zawsze moesz edytowa lub wykasowa opini. Usunicie opini skutkuje usuniciem wizerunku z miejsca publicznego strony. Jeli nie jeste penoletni o zgod zapytaj osoby dorose: Rodzica, Opiekuna ..."
            type="text"
            rows="12"
            name="opinion"
            value={user.opinion}
          />
        </div>
      </form>
      <div className="profileButtons btn-editProfileButton">
        <button className="btn btn-green btn-capitalize">zapisz</button>
      </div>
    </div>
  );
};

export default EditProfile;
